<template>
    <div class="listName" @click.stop>
        <div v-for="key in listName" :class="{setClass:key.set,noSetClass:!key.set}" @click="key.set=!key.set">
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{key.name}}
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                listName:''
            }
        },
        props:['data'],
        mounted:function () {
            this.listName=this.data;
        },
        updated:function () {
            this.$emit('crContent',this.listName)
        }
    }
</script>
<style scoped>
    .noSetClass{
        background: url("./../../actual/publicImg/set0.png") no-repeat;
        background-position: 2px;
    }
    .setClass{
        background: url("./../../actual/publicImg/set1.png") no-repeat;
        background-position: 2px;
    }
    .listName{
        display:flex;
        flex-wrap:wrap;
        position: absolute;
        top:20px;
        z-index: 5;
        padding:10px 10px 10px 10px;
        background-color:#122038;
    }
    .listName>div{
        display: flex;
        font-size:12px;
        color: white;
        padding: 5px 10px 5px 0px;
    }
</style>


