import airQuery from '../page/airQuery.vue'
import airLineQuery from '../page/airLineQuery.vue'
import cityQuery from '../page/cityQuery.vue'
import setting from '../page/setting.vue'


export default{
    routes:[
        {path:'/airQuery', component:airQuery},
        {path:'/airLineQuery', component:airLineQuery},
        {path:'/cityQuery', component:cityQuery},
        {path:'/setting', component:setting},
        // {path:'*', redirect:'/setting'}
    ]
}



