import css from './css/query.vue'
import allcss from './css/allCss.vue'
import csswar from './css/warehouse.vue'
import spae_ from './css/standard/spae_.vue'
import cetn_ from './css/standard/cetn_.vue'
import Vue from 'vue'

import App from './App.vue'

import VueRouter from 'vue-router'


import store from './store/'

import routerConfig from './router/routerConfig.js' //引入路由
import staticData from './JsonData/staticData'     //请求静态资源数据

Vue.use(VueRouter);


const router=new VueRouter({
    routes:routerConfig.routes,
    // mode: 'history'
});
var vm=new Vue({
    router,
    store,
    el: '#air',
    render: h => h(App)
});



